  color: #fff;
  background: rgb(221, 42, 42) !important;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
}

.eventinfo-glow-on-hover:before {
  content: "";
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  -webkit-filter: blur(5px);
          filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  -webkit-animation: glowing 20s linear infinite;
          animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

.eventinfo-glow-on-hover:active {
  color: #000;
}

.eventinfo-glow-on-hover:active:after {
  background: transparent;
}

.eventinfo-glow-on-hover:hover:before {
  opacity: 1;
}

.eventinfo-glow-on-hover:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(207, 51, 51);
  left: 0;
  top: 0;
  border-radius: 10px;
}

